@import url(https://stackpath.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
}

body{
  background-color: #f3f2f8;
}

.app{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  padding: 20px;
}

.app__header {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin-bottom: 20px;
  -webkit-justify-content: space-between;
          justify-content: space-between;

}


.app__left{
  -webkit-flex: 0.9 1;
          flex: 0.9 1;
}

.app__stats{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
} 

@media (max-width: 990px) {
  .app{
    -webkit-flex-direction: column;
            flex-direction: column;
  }
}
.infoBox {
  -webkit-flex: 1 1;
          flex: 1 1;
}

.infoBox:not(:last-child) {
  margin-right: 10px;
}

.infoBox--selected {
  border-top: 10px solid rgb(87, 128, 26);
}

.infoBox--red {
  border-color: rgb(155, 2, 2);
}

.infoBox__cases--green {
  color: rgb(82, 226, 82) !important;
}

.infoBox__cases {
  color: #cc1034;
  font-weight: 600;
  font-size: 1.75rem;
  margin-bottom: 0.5rem;
}

.infoBox__total {
  color: #ffffff;
  font-weight: 700 !important;
  font-size: 0.8rem !important;
  margin-top: 15px !important;
}
.map {
    height: 500px;
    background-color: rgb(62, 62, 223);
    padding: 1rem;
    border-radius: 20px;
    margin-top: 16px;
    box-shadow: 0 0 8px -4px rgba(0, 0, 0, 0.5);
  }
  
  .info-flag img {
    width: 100px;
    border-radius: 5px;
  }
  
  .info-flag {
    height: 80px;
    width: 100%;
    background-size: cover;
    border-radius: 8px;
  }
  
  .info-confirmed,
  .info-deaths,
  .info-recovered {
    font-size: 16px;
    margin-top: 5px;
  }
  
  .info-name {
    font-size: 20px;
    font-weight: bold;
    color: black;
  }
.table{
    margin-top: 20px;
    overflow: scroll;
    height: 400px;
    color: #000000;
}

.table tr{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    
    
}

.table td{
    padding: 0.5rem;
    
}

.table tr:nth-of-type(odd){
    background-color: #f3f2f8;
}



