.table{
    margin-top: 20px;
    overflow: scroll;
    height: 400px;
    color: #000000;
}

.table tr{
    display: flex;
    justify-content: space-between;
    
    
}

.table td{
    padding: 0.5rem;
    
}

.table tr:nth-of-type(odd){
    background-color: #f3f2f8;
}


